:root {
  --font-body: "Roboto", sans-serif;
}

.material-icons {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: "liga";
}

.icons--white {
  color: #ffff;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --header-bg-dark: hsl(0, 0%, 13%);
  --header-btn-color: hsl(208, 100%, 62%);
  --header-search-btn: hsl(0, 0%, 20%);
  --header-search-placeholder: hsl(0, 0%, 51%);
  --header-search-input: hsl(0, 0%, 7%);
  --body-bg-dark: hsl(0, 0%, 9%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--body-bg-dark);
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  color: white;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p,
span {
  color: white;
  margin: 0;
}

h2 {
  font-size: 0.9375rem;
  font-weight: 500;
}

.container {
  padding: 0 1.5rem;
  max-width: 115.625rem;
  margin: 0 auto;
}

.container--flex {
  padding: 0 1.5rem;
  max-width: 100vw;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex--row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex--col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.container--sidebar {
  display: flex;
  flex-wrap: wrap;
}

.content--sidebar-main {
  flex: 1 1 70%;
  /* border: 2px solid white; */
}

.content--sidebar--second {
  flex: 1 1 20%;
  /* border: 2px solid white; */
}

.aside__container {
  display: flex;
  /* border: 2px solid magenta; */
}

button {
  border: none;
  border-radius: 0.3125rem;
  cursor: pointer;
}

.signUp__btn {
  max-width: 7.1875rem;
  padding: 0.5rem;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 500;
  border: 2px solid var(--header-btn-color);
  color: var(--header-btn-color);
  background: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.signUp__btn span {
  display: inline-block;
  margin-right: 0.625rem;
}

header {
  background-color: var(--header-bg-dark);
}

.header__logo__hamburger {
  padding: 0.5rem;
}

.header__logo__youtube {
  padding: 1rem;
}

.logo__youtube {
  width: 5.625rem;
  height: 1.25rem;
}

.header__search__input__container {
  min-width: 35.9375rem;
  min-height: 2rem;
}
.header__search__input__container input {
  flex: 1;
  height: 2rem;
  background-color: var(--header-search-input);
  border: none;
}
.header__search__input__container input::placeholder {
  color: var(--header-search-placeholder);
  font-size: 1rem;
  font-weight: bold;
  padding-left: 0.3125rem;
}
.header__search__input__container .search__icon {
  text-align: center;
  width: 4.0625rem;
}

.search__icon {
  background-color: var(--header-search-btn);
}

.header__search__voice {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  text-align: center;
}

.aside__container {
  margin-bottom: 0.625rem;
}
.aside__container span {
  color: white;
  font-size: 1rem;
}
.aside__container p {
  display: inline-block;
}
.aside__container a {
  font-size: 0.8125rem;
}
.aside__container h2 {
  overflow: hidden;
}

.aside__content {
  margin-left: 0.625rem;
  margin-bottom: 0.625rem;
}

.aside__name a {
  color: var(--header-search-placeholder);
}
.aside__name span {
  color: var(--header-search-placeholder);
}

.aside__views p {
  color: var(--header-search-placeholder);
  font-size: 0.75rem;
  font-weight: 700;
}

.aside__new {
  width: 2.0625rem;
  height: 0.875rem;
  background-color: var(--header-search-btn);
  display: flex;
  justify-content: center;
  align-items: center;
}
.aside__new p {
  color: var(--header-search-placeholder);
  font-size: 0.75rem;
  font-weight: 700;
}

.video__section__view__buttons__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem;
  /* border: 2px solid magenta; */
}

.video__logo__subscribers__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border: 2px solid magenta; */
}

.video__logo__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.channel__name__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.channel__name {
  display: flex;
  justify-content: center;
  align-items: center;
}

summary {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  border-bottom: 1px solid var(--header-search-placeholder);
}

.summary__text {
  width: 50%;
  margin-left: 3.125rem;
}

.comment__section__comments {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 1.875rem;
}
.comment__section__comments span {
  margin-right: 0.3125rem;
}
.comment__section__comments button {
  margin-left: 1.875rem;
}

.comment__section__input__container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 1.875rem;
}

.comment__input__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  margin-left: 0.9375rem;
}

.input__container input {
  background-color: var(--body-bg-dark);
  width: 100%;
  border: none;
}
.input__container input::placeholder {
  color: var(--header-search-placeholder);
  font-style: var(--font-body);
  font-size: 0.75rem;
  font-weight: 700;
}

.comment__input__border__div {
  width: 100%;
  flex: 1;
  border: 1px solid var(--header-search-placeholder);
}

.posted__comments__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.avatar {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.avatar--comment {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
}

.main__iframe {
  width: 80rem;
  height: 45rem;
}

.aside__iframe {
  width: 10.5rem;
  height: 5.875rem;
}